.heading-lv2 {
    color: #000;
    font-family: var(--font-heading);
    font-size: 3.8rem;
    font-weight: 700;
    line-height: 1.27;
    letter-spacing: -0.76px;
  }
  
  .main-content {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
  
  .btn {
    color: #fff;
    display: inline-block;
    min-width: 118px;
    padding: 0 16px;
    line-height: 50px;
    text-align: center;
    font-size: 1.6rem;
    font-weight: 600;
    border-radius: 999px;
    background: rgb(9, 128, 124);
  }
  
  .btn:hover {
    cursor: pointer;
    opacity: 0.9;
  }
  
  .line-clamp {
    display: -webkit-box;
    -webkit-line-clamp: var(--line-champ, 1);
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  
  .line-clamp.line-2 {
    --line-champ: 2;
  }
  
  .break-all {
    word-break: break-all;
  }
  
  /* ===============================header=============================== */
  .header {
    background: #000000;
  }
  
  .header.fixed {
    z-index: 1;
    position: sticky;
    top: -28px;
  }
  
  .header .body {
    /* tác động vào thẻ body là con của thẻ header */
    display: flex;
    align-items: center;
    padding: 36px 0 8px;
  }
  
  .nav {
    margin-left: auto;
  }
  
  .nav ul {
    display: flex;
  }
  
  .nav a {
    position: relative;
    color: rgb(255, 255, 255);
    font-size: 1.6rem;
    padding: 8px 21px;
  }
  
  .nav li.active a,
  .nav a:hover {
    color: rgb(9, 128, 124);
    /* font-weight: 600; */
    text-shadow: 1px 0 0 currentColor;
  }
  
  .nav li.active a::after {
    position: absolute;
    display: block;
    content: "";
    left: 21px;
    bottom: 6px;
    width: 12px;
    height: 2px;
    border-radius: 1px;
    background: rgb(9, 128, 124);
  }
  
  .header .btn-sign-up {
    min-width: 144px;
  }
  
  .header .action {
    margin-left: 49px;
  }
  
  /* ===========================hero========================== */
  .hero {
    padding: 56px 0 65px;
    background: rgb(9, 128, 124);
  }
  
  .hero .body {
    display: flex;
  }
  
  /* ===========================hero left========================== */
  .hero .media-block {
    position: relative;
    width: 48%;
  }
  
  .hero .media-block .img {
    width: 470px;
    height: 685px;
    object-fit: cover;
    border-radius: 20px;
  }
  
  .hero .hero-summary {
    padding: 24px;
    width: 270px;
    position: absolute;
    bottom: 48px;
    right: 0;
    border-radius: 12px;
    background: #e0cfcf;
  }
  
  .hero-summary .item {
    display: flex;
    align-items: center;
  }
  
  .hero-summary .item + .item {
    margin-top: 22px;
  }
  
  .hero-summary .icon {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .hero-summary .info {
    margin-left: 16px;
  }
  
  .hero-summary .label {
    font-size: 1.4rem;
  }
  
  /* ===========================hero right========================== */
  .hero .content-block {
    width: 52%;
    padding-left: 130px;
    padding-top: 64px;
  }
  
  .hero .heading {
    color: #04041d;
    font-family: var(--font-heading);
    font-size: 5.8rem;
    font-weight: 700;
    line-height: 1.17;
    letter-spacing: -1.16;
  }
  
  .hero .desc {
    margin-top: 22px;
    color: black;
  }
  
  .hero .cta-group {
    display: flex;
    align-items: center;
    margin-top: 38px;
  }
  
  .hero-cta {
    min-width: 180px;
    line-height: 60px;
    background-color: var(--primary-color);
  }
  
  .hero .watch-video {
    border: none;
    margin-left: 28px;
    background-color: transparent;
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  .hero .watch-video .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 40px;
    background: #000;
    box-shadow: 0px 8px 16px 0px;
  }
  
  .hero .watch-video span {
    margin-left: 14px;
    font-size: 1.8rem;
    font-weight: 600;
    line-height: 1.67;
  }
  
  .hero .desc-recent {
    margin-top: 48px;
  }
  
  .hero .stats {
    margin-top: 8px;
  }
  
  .hero .stats strong {
    margin-right: 15px;
    font-family: var(--font-heading);
    font-size: 4.4rem;
    font-weight: 700;
    line-height: 1.23;
  }
  
  .hero .stats strong:nth-child(2) {
    margin-left: 28px;
  }
  
  /* ===========================popular========================== */
  .popular {
    padding: 65px;
    background-color: #ffffff;
    
  }
  
  .popular-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .popular-top .desc {
    margin-top: 16px;
    width: 458px;
    color: #04041d;
    font-size: 1.8rem;
    line-height: 1.67;
  }
  
  .popular .controls {
    display: flex;
    gap: 18px;
  }
  
  .popular-top .control-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: var(--primary-color);
    border: 1px solid var(--primary-color);
  }
  
  .popular-top .control-btn:hover {
    background: var(--primary-color);
    color: #e0cfcf;
  }
  
  .popular .course-list {
    gap: 30px;
    display: flex;
    margin-top: 55px;
  }
  
  .popular.course-item {
    flex: 1;
    border-radius: 12px;
    border: 1px solid #babec0;
    background: #0e0d0d;
  }
  
  .popular.course-item:hover {
    border: 12px;
    box-shadow: 0px 18px 36px 0px rgba(0, 0, 0, 0.05);
  }
  
  .popular .course-item .thumb {
    border-radius: 12px 12px 0px 0px;
    width: 100%;
    object-fit: cover;
    height: 278px;
  }
  
  .popular .course-item .info {
    padding: 16px 22px 22px;
  }
  
  .popular .course-item .foot,
  .popular .course-item .rating,
  .popular .course-item .head {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .popular .course-item .title a {
    color: #023654;
    font-size: 1.8rem;
    font-weight: 600;
    line-height: 1.67;
  }
  
  .popular .rating .value {
    margin-left: 6px;
    font-size: 1.6rem;
    line-height: 1.75;
  }
  
  .popular .course-item .desc {
    margin-top: 6px;
    font-size: 1.4rem;
    line-height: 1.68;
  }
  
  .popular .course-item .foot {
    margin-top: 12px;
  }
  
  .popular .course-item .btn-book {
    border: none;
  }
  
  .popular .course-item .btn-book:hover {
    background: var(--primary-color);
  }
  
  /* ===========================feedback========================== */
  .feedback {
    margin-top: 0px;
    padding: 40px 0;
    background: #2e2100;
  }
  
  .feedback-list {
    display: flex;
    overflow: hidden;
  }
  
  .feedback-item {
    width: 100%;
    display: flex;
    flex-shrink: 0;
  }
  
  .feedback-item .avatar {
    width: 72px;
    height: 72px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .feedback-item .info .member-name {
    margin-top: 18px;
    font-size: 2.4rem;
    font-weight: 700;
    line-height: 1.42;
    font-family: var(--font-heading);
  }
  
  .feedback-item .info .desc {
    margin-top: 4px;
    color: #bfbcb2;
    font-size: 1.4rem;
    line-height: 1.86;
  }
  
  .feedback-item .dots {
    gap: 6px;
    display: flex;
    margin-top: 28px;
  }
  
  .feedback-item .dot {
    cursor: pointer;
    height: 6px;
    width: 6px;
    border-radius: 50%;
    background: #0c0c0d;
    cursor: pointer;
  }
  
  /* viết liền .dot.active thể hiện 1 phần tử có đồng thời cả 2 class này mới thực hiện được */
  .feedback-item .dot.active {
    background: var(--primary-color);
    cursor: default;
  }
  
  .feedback-item .content {
    width: 80%;
    margin-left: auto;
  }

  .feedback-item .content >img {
    width: 5%;
    margin-bottom: 40px;
  }
  
  .feedback-item blockquote {
    font-size: 2rem;
    font-style: italic;
    color: #fff;
    line-height: 1.54;
    
  }
  
  /* ===========================features 1========================== */
  
  .features {
    margin-top: 135px;
    padding: 65px 0;
  }
  
  .features .body {
    display: flex;
    justify-content: space-between;
  }
  
  .features .images {
    display: flex;
    gap: 30px;
  }
  
  .features .images img {
    width: 270px;
    height: 405px;
    object-fit: cover;
    border-radius: 16px;
  }
  .features .lower {
    margin-top: 34px;
  }
  
  .features .content {
    width: 41%;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  
  .features .desc {
    margin-top: 16px;
    font-size: 1.6rem;
    line-height: 1.75;
  }
  
  .features .cta-btn {
    margin-top: 32px;
    width: 137px;
    height: 50px;
    background: var(--primary-color);
  }
  
  /* ===========================features 2========================== */
  .features-2nd {
    margin-top: 70px;
  }
  
  .features-2nd .body {
    flex-direction: row-reverse;
  }
  
  .features-2nd .content {
    width: 49%;
  }
  
  .features-2nd .images img {
    width: 470px;
    height: 440px;
  }
  
  /* ===============================footer=============================== */
  
  